<template>
  <div>
    <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
    </base-header>

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col">
          <div class="card shadow border-0" style="height: 600px">
            <trade-detail
              v-on:listen_check1="set_check_detail"
              :trade_detail1_num="trade_detail_num"
              v-if="check_detail"
            />
            <div v-else>
              <div>
                <select
                  v-model="selected_si_code"
                  class="form-control sel1"
                  @change="getGugun()"
                  id="sel1"
                >
                  <option
                    v-for="(si1, index) in si_list"
                    :value="si1.sidoCode"
                    :key="index"
                  >
                    {{ si1.sidoName }}
                  </option>
                </select>
                <select
                  v-model="selected_gugun_code"
                  class="form-control sel1"
                  @change="getDong()"
                  id="sel1"
                >
                  <option
                    v-for="(gugun1, index) in gugun_list"
                    :key="index"
                    :value="gugun1.gugunCode"
                  >
                    {{ gugun1.gugunName }}
                  </option>
                </select>
                <select
                  v-model="selected_dong_code"
                  class="form-control sel1"
                  @change="setDongName()"
                  id="sel1"
                >
                  <option
                    v-for="(dong1, index) in dong_list"
                    :value="dong1.dongCode"
                    :key="index"
                  >
                    {{ dong1.dongName }}
                  </option>
                </select>

                <button
                  class="btn btn-primary sel1 btn2"
                  v-on:click="search1()"
                >
                  검색
                </button>
              </div>

              <table class="table table-hover">
                <thead>
                  <tr>
                    <th style="width: 10%" class="table_font">번호</th>
                    <th style="width: 50%" class="table_font">이름</th>
                    <th style="width: 15%" class="table_font">종류</th>
                    <th style="width: 15%" class="table_font">가격(만)</th>
                    <th style="width: 10%" class="table_font"></th>
                  </tr>
                </thead>
                <tbody v-for="(hl1, index) in paginatedData" :key="index">
                  <tr
                    v-on:click="
                      getTradeDetail(hl1.tradeId, hl1.roadAddr, hl1.sigugun)
                    "
                  >
                    <td class="table_font">{{ hl1.tradeId }}</td>
                    <td class="table_font">{{ hl1.houseName }}</td>
                    <td class="table_font">{{ hl1.dealType }}</td>
                    <td class="table_font">{{ hl1.dealPrice }}</td>
                    <td class="table_font">
                      <i class="ni ni-favourite-28 text-red"> </i>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="btn-cover" v-if="!check_detail">
              <button
                :disabled="pageNum === 0"
                @click="prevPage"
                class="page-btn rounded"
              >
                이전
              </button>
              <span class="page-count"
                >{{ pageNum + 1 }} / {{ pageCount }} 페이지</span
              >
              <button
                :disabled="pageNum >= pageCount - 1"
                @click="nextPage"
                class="page-btn rounded"
              >
                다음
              </button>
            </div>
          </div>
        </div>
        <div class="col">
          <div class="card shadow border-0" style="height: 600px">
            <kakaomap
              v-bind:searchString="searchString"
              :house_list="house_list"
              :house_detail_name="trade_detail_name"
              :house_detail_sigugun="trade_detail_sigugun"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="empty-box"></div>
  </div>
</template>
<script>
import Kakaomap from "@/components/kakaomap.vue";
import http from "@/utils/http-common.js";
import TradeDetail from "@/components/Trade/TradeDetail1.vue";

export default {
  created() {
    http.get(`/trade/sido`).then(({ data }) => {
      this.si_list = data;
    });
  },
  components: {
    Kakaomap,
    TradeDetail,
  },
  data() {
    return {
      searchString: "11",
      house_list: [],
      nav: null,
      si_list: [{ sidoName: "서울특별시" }, { sidoName: "부산광역시" }],
      gugun_list: [{ text: "동래구" }, { text: "해운대구" }],
      dong_list: [{ text: "낙민동" }, { text: "명륜동" }],
      selected_si: null,
      selected_gugun: null,
      selected_dong: null,
      selected_si_code: null,
      selected_gugun_code: null,
      selected_dong_code: null,
      pageNum: 0,
      pageSize: 8,
      trade_detail_num: null,
      check_detail: false,
      trade_detail_name: null,
      trade_detail_sigugun: null,
    };
  },
  computed: {
    pageCount() {
      let listLeng = this.house_list.length,
        listSize = this.pageSize,
        page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;

      /*
      아니면 page = Math.floor((listLeng - 1) / listSize) + 1;
      이런식으로 if 문 없이 고칠 수도 있다!
      */
      return page;
    },
    paginatedData() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize;
      return this.house_list.slice(start, end);
    },
  },
  methods: {
    nextPage() {
      this.pageNum += 1;
    },
    prevPage() {
      this.pageNum -= 1;
    },
    search1() {
      this.searchString =
        this.selected_si + " " + this.selected_gugun + " " + this.selected_dong;

      http
        .get(`/trade/tradeindong`, {
          params: { addr: this.searchString },
        })
        .then(({ data }) => {
          this.house_list = data;
        });
    },
    getGugun() {
      http
        .get(`/trade/gugun`, { params: { sido: this.selected_si_code } })
        .then(({ data }) => {
          this.gugun_list = data;
        });
      this.setSiName();
    },
    getDong() {
      http
        .get(`/trade/dong`, { params: { gugun: this.selected_gugun_code } })
        .then(({ data }) => {
          this.dong_list = data;
        });
      this.setGugunName();
    },
    setSiName() {
      this.si_list.forEach((element) => {
        if (element.sidoCode == this.selected_si_code) {
          this.selected_si = element.sidoName;
        }
      });
    },
    setGugunName() {
      this.gugun_list.forEach((element) => {
        if (element.gugunCode == this.selected_gugun_code) {
          this.selected_gugun = element.gugunName;
        }
      });
    },
    setDongName() {
      this.dong_list.forEach((element) => {
        if (element.dongCode == this.selected_dong_code) {
          this.selected_dong = element.dongName;
        }
      });
    },
    getTradeDetail(num, name, sigugun) {
      this.trade_detail_num = num;
      this.check_detail = true;

      this.trade_detail_name = name;
      this.trade_detail_sigugun = sigugun;
    },
    set_check_detail() {
      this.check_detail = false;
    },
  },
  mounted() {},
};
</script>
<style scoped>
.bg-gradient-success {
  background: linear-gradient(87deg, #2ac1e7 0, #f16d99 100%) !important;
}

.form-control {
  float: left;
}
.sel1 {
  float: left;
  width: 150px;
  margin-left: 8px;
  margin-bottom: 5px;
  margin-top: 5px;
}

.btn2 {
  width: 100px;
}

.btn-cover {
  position: absolute;
  margin-left: 230px;
  margin-top: 550px;
}

.empty-box {
  margin-top: 300px;
}
</style>
