<template>
  <div>
    <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
      <div class="row">
        <div class="col-xl-3 col-lg-6"></div>
      </div>
    </base-header>

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col">
          <div class="card shadow">
            <div class="card-body">
              <!--board 내용 시작-->
              <div class="row icon-examples">
                <table class="table table-hover">
                  <thead>
                    <tr>
                      <th style="width: 10%" class="table_font">번호</th>
                      <th style="width: 60%" class="table_font">제목</th>
                      <th style="width: 10%" class="table_font">작성자</th>
                      <th style="width: 10%" class="table_font">조회수</th>
                      <th style="width: 10%" class="table_font">작성일</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td class="table_font">{{ main_contents.boardNo }}</td>
                      <td class="table_font">{{ main_contents.boardTitle }}</td>
                      <td class="table_font">{{ main_contents.memberId }}</td>
                      <td class="table_font">{{ main_contents.boardCnt }}</td>
                      <td class="table_font">
                        {{ main_contents.boardRecent }}
                      </td>
                    </tr>
                    <td class="table_font">{{ main_contents.boardContent }}</td>
                    <td class="table_font"></td>
                    <td class="table_font"></td>
                    <td class="table_font"></td>
                    <td class="table_font"></td>
                  </tbody>
                </table>
              </div>

              <!--board 내용 끝 -->
            </div>
          </div>
          <button
            type="button"
            class="btn btn-success"
            v-on:click="goupdateboard1()"
            id="goback1"
            v-if="checkmyboard()"
          >
            수정
          </button>
          <button
            type="button"
            class="btn btn-danger"
            v-on:click="deleteboard1()"
            id="goback1"
            v-if="checkmyboard()"
          >
            삭제
          </button>
          <div class="card shadow" id="commentsbox">
            <div class="card body">
              <table class="table">
                <tbody id="reply_table">
                  <tr v-for="(reply, index) in replys" :key="index">
                    <td style="width: 10%" class="replytd">
                      {{ reply.memberId }}
                    </td>
                    <td
                      style="width: 65%"
                      class="replytd"
                      v-if="reply.now_state"
                    >
                      {{ reply.replyContent }}
                    </td>
                    <td
                      style="width: 65%"
                      class="replytd"
                      v-if="!reply.now_state"
                    >
                      <textarea
                        class="form-control"
                        rows="1"
                        id="comment"
                        v-model="reply.replyContent"
                      ></textarea>
                    </td>
                    <td style="width: 10%" class="replytd">
                      {{ reply.replyRecent }}
                    </td>
                    <td style="width: 10%" class="replytd">
                      <button
                        type="button"
                        class="btn btn-light"
                        v-if="IDcheck(reply.memberId)"
                        v-on:click="updateReply(reply.replyNo)"
                      >
                        {{ reply.label }}
                      </button>
                      <button
                        type="button"
                        class="btn btn-danger"
                        v-if="IDcheck(reply.memberId)"
                        v-on:click="deleteReply(reply.replyNo)"
                      >
                        삭제
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
              <div class="form-group" id="commentsin">
                <label for="comment"></label>
                <textarea
                  class="form-control"
                  rows="4"
                  id="comment"
                  v-model="reply_content1"
                ></textarea>
                <button
                  type="button"
                  class="btn btn-light"
                  v-on:click="createreply()"
                  id="commentcreate"
                >
                  답글달기
                </button>
              </div>
            </div>
          </div>
          <button
            type="button"
            class="btn btn-primary"
            v-on:click="goback()"
            id="goback1"
          >
            목록으로
          </button>
        </div>
      </div>
    </div>
    <div class="empty-box"></div>
  </div>
</template>

<script>
import http from "@/utils/http-common.js";
import { mapGetters } from "vuex";

export default {
  created() {
    let url2 = "/board/" + this.$route.params.listno;
    http.get(url2).then(({ data }) => {
      this.main_contents = data;
    });

    let url = "/reply/" + this.$route.params.listno;
    http.get(url).then(({ data }) => {
      this.replys = data;

      this.replys.forEach((element) => {
        element["now_state"] = true;
        element["label"] = "수정";
      });
    });
  },
  computed: {
    ...mapGetters(["getUserId"]),
  },
  components: {},
  data() {
    return {
      main_contents: {},
      reply_content1: null,
      replys: [],
      updatelabel: "수정",
    };
  },
  methods: {
    gogo(num) {
      alert(num);
    },
    goback() {
      this.$router.go(-1);
    },
    IDcheck(ID1) {
      if (ID1 == this.getUserId) return true;
      else return false;
    },
    checkmyboard() {
      if (this.main_contents.memberId == this.getUserId) return true;
      else return false;
    },
    goupdateboard1() {
      this.$router.push({ name: "BoardUpdate", params: this.main_contents });
    },
    deleteboard1() {
      let url = "/board/" + this.main_contents.boardNo;
      http.delete(url).then(({ data }) => {
        let msg = "게시글 삭제시 문제가 발생했습니다.";
        if (data === "success") {
          msg = "게시글 삭제가 완료되었습니다.";
        }
        alert(msg);
        this.$router.go(-1);
      });
    },
    getupdateReplyState() {
      return true;
    },
    createreply() {
      if (this.getUserId == null) {
        alert("로그인해주세요.");
      } else {
        http
          .post(`/reply`, {
            boardNo: this.main_contents.boardNo,
            memberId: this.getUserId,
            replyContent: this.reply_content1,
          })
          .then(({ data }) => {
            let msg = "등록 처리시 문제가 발생했습니다.";
            if (data === "success") {
              msg = "답글 등록이 완료되었습니다.";
            }
            alert(msg);
            this.$router.go();
          });
      }
    },
    updateReply(num) {
      this.replys.forEach((element) => {
        if (element.replyNo == num) {
          element.now_state = !element.now_state;
          if (element.now_state == true) {
            let url = "/reply";
            http
              .put(url, {
                boardNo: this.main_contents.boardNo,
                memberId: this.getUserId,
                replyContent: element.replyContent,
                replyNo: num,
              })
              .then(({ data }) => {
                let msg = "등록 처리시 문제가 발생했습니다.";
                if (data === "success") {
                  msg = "답글 수정이 완료되었습니다.";
                }
                alert(msg);
                this.$router.go();
              });
          }
          if (element.label == "수정") element.label = "수정완료";
          else element.label = "수정";
        }
      });
    },
    deleteReply(num) {
      let url = "/reply/" + num;
      http.delete(url).then(({ data }) => {
        let msg = "삭제시 문제가 발생했습니다.";
        if (data === "success") {
          msg = "답글 삭제가 완료되었습니다.";
        }
        alert(msg);
        this.$router.go();
      });
    },
  },
};
</script>

