<template>
  <div>
    <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
      <div class="row">
        <div class="col-xl-3 col-lg-6"></div>
      </div>
    </base-header>

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col">
          <div class="card shadow" id="commentsbox">
            <div class="card body">
              <table class="table">
                <tbody id="reply_table">
                  <tr>
                    <td style="width: 5%" class="replytd">제목</td>
                    <td style="width: 95%" class="replytd">
                      <div class="form-group">
                        <label for="title_label"></label>
                        <input
                          ref="title_ref"
                          type="text"
                          class="form-control"
                          placeholder="제목을 입력해주세요.."
                          v-model="create_contents.boardTitle"
                          id="title1"
                        />
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
              <table class="table">
                <tbody id="reply_table">
                  <tr>
                    <td style="width: 5%" class="replytd2">내용</td>
                    <td style="width: 95%" class="replytd2">
                      <div class="form-group">
                        <label for="comment_label"></label>
                        <textarea
                          ref="content_ref"
                          class="form-control"
                          rows="8"
                          placeholder="내용을 입력해주세요.."
                          v-model="create_contents.boardContent"
                          id="content1"
                        ></textarea>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <button
            type="button"
            class="btn btn-primary"
            v-on:click="goback()"
            id="goback1"
          >
            목록으로
          </button>
          <button
            type="button"
            class="btn btn-success"
            v-on:click="goenroll()"
            id="enroll1"
          >
            글 등록
          </button>
        </div>
      </div>
    </div>
    <div class="empty-box"></div>
  </div>
</template>

<script>
import http from "@/utils/http-common.js";
import { mapGetters } from "vuex";

export default {
  created() {},
  components: {},
  data() {
    return {
      create_contents: { boardTitle: "", boardContent: "" },
    };
  },
  computed: {
    ...mapGetters(["getUserId"]),
  },
  methods: {
    gogo(num) {
      alert(num);
    },
    goback() {
      this.$router.go(-1);
    },
    goenroll() {
      let err = true;
      let msg = "";
      !this.create_contents.boardTitle &&
        ((msg = "제목 입력해주세요"),
        (err = false),
        this.$refs.title_ref.focus());
      err &&
        !this.create_contents.boardContent &&
        ((msg = "내용 입력해주세요"),
        (err = false),
        this.$refs.content_ref.focus());

      if (!err) alert(msg);
      else {
        http
          .post(`/board`, {
            memberId: this.getUserId,
            boardTitle: this.create_contents.boardTitle,
            boardContent: this.create_contents.boardContent,
          })
          .then(({ data }) => {
            let msg = "등록 처리시 문제가 발생했습니다.";
            if (data === "success") {
              msg = "등록이 완료되었습니다.";
            }
            alert(msg);
            this.$router.push({ name: "Board" });
          });
      }
    },
  },
};
</script>

