<template>
  <div>
    <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
      <div class="row">
        <div class="col-xl-3 col-lg-6"></div>
      </div>
    </base-header>

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col">
          <div class="card shadow border-0" style="height: 600px">
            <trade-detail
              v-on:listen_check1="set_check_detail"
              :trade_detail1_num="trade_detail_num"
              v-if="check_detail"
            />
            <div v-else>
              <table class="table table-hover">
                <thead>
                  <tr>
                    <th style="width: 10%" class="table_font">번호</th>
                    <th style="width: 50%" class="table_font">이름</th>
                    <th style="width: 15%" class="table_font">종류</th>
                    <th style="width: 15%" class="table_font">가격(만)</th>
                    <th style="width: 10%" class="table_font"></th>
                  </tr>
                </thead>
                <tbody v-for="(hl1, index) in paginatedData" :key="index">
                  <tr
                    v-on:click="
                      getTradeDetail(hl1.tradeId, hl1.roadAddr, hl1.sigugun)
                    "
                  >
                    <td class="table_font">{{ hl1.tradeId }}</td>
                    <td class="table_font">{{ hl1.houseName }}</td>
                    <td class="table_font">{{ hl1.dealType }}</td>
                    <td class="table_font">{{ hl1.dealPrice }}</td>
                    <td class="table_font" @click.stop>
                      <button
                        class="btn btn-danger btn btndeletelike"
                        v-on:click="deleteLike(hl1.tradeId)"
                      >
                        삭제
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="btn-cover" v-if="!check_detail">
              <button
                :disabled="pageNum === 0"
                @click="prevPage"
                class="page-btn rounded"
              >
                이전
              </button>
              <span class="page-count"
                >{{ pageNum + 1 }} / {{ pageCount }} 페이지</span
              >
              <button
                :disabled="pageNum >= pageCount - 1"
                @click="nextPage"
                class="page-btn rounded"
              >
                다음
              </button>
            </div>
          </div>
        </div>
        <div class="col">
          <div class="card shadow border-0" style="height: 600px">
            <kakaomap
              :house_detail_name="trade_detail_name"
              :house_detail_sigugun="trade_detail_sigugun"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Kakaomap from "@/components/kakaomap.vue";
import http from "@/utils/http-common.js";
import { mapGetters } from "vuex";
import TradeDetail from "@/components/Trade/TradeDetail1.vue";

export default {
  created() {
    if (this.getLoginState == false) {
      alert("로그인이 필요합니다.");
      this.$router.push({ name: "MemberLogin" });
    } else {
      let url = "/locfavor/" + this.getUserId;
      http.get(url).then(({ data }) => {
        this.like_boards = data;
      });
    }
  },
  components: {
    Kakaomap,
    TradeDetail,
  },
  computed: {
    ...mapGetters(["getUserId", "getLoginState"]),
    pageCount() {
      let listLeng = this.like_boards.length,
        listSize = this.pageSize,
        page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;

      /*
      아니면 page = Math.floor((listLeng - 1) / listSize) + 1;
      이런식으로 if 문 없이 고칠 수도 있다!
      */
      return page;
    },
    paginatedData() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize;
      return this.like_boards.slice(start, end);
    },
  },
  data() {
    return {
      like_boards: [],
      nav: null,
      pageNum: 0,
      pageSize: 7,
      trade_detail_num: null,
      trade_detail_name: null,
      trade_detail_sigugun: null,
      check_detail: false,
    };
  },
  methods: {
    nextPage() {
      this.pageNum += 1;
    },
    prevPage() {
      this.pageNum -= 1;
    },
    getTradeDetail(num, name, sigugun) {
      this.trade_detail_num = num;
      this.check_detail = true;

      this.trade_detail_name = name;
      this.trade_detail_sigugun = sigugun;
    },
    set_check_detail() {
      this.check_detail = false;
    },
    deleteLike(num) {
      http
        .delete("/locfavor", {
          data: {
            memberId: this.getUserId,
            tradeId: num,
          },
        })
        .then(({ data }) => {
          let msg = "등록 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "관심지역을 삭제하였습니다.";
          }

          alert(msg);
          this.$router.go();
        });
    },
    hello() {
      alert("he");
    },
  },
};
</script>
<style scoped>
.bg-gradient-success {
  background: linear-gradient(87deg, #2ac1e7 0, #f16d99 100%) !important;
}

.btn-cover {
  position: absolute;
  margin-left: 250px;
  margin-top: 560px;
}

.btndeletelike {
  width: 80px;
  height: 40px;
}
</style>
