import { createRouter, createWebHashHistory } from "vue-router";

import DashboardLayout from "@/layout/DashboardLayout";

import Home1 from "../views/Home1.vue";
import Board1 from "../views/Board1.vue";
import BoardDetail1 from "../components/Board/BoardDetail1.vue";
import BoardRegister1 from "../components/Board/BoardRegister1.vue";
import BoardUpdate1 from "../components/Board/BoardUpdate1.vue";
import Trade1 from "../views/Trade1.vue";
import Like1 from "../views/Like1.vue";
import News1 from "../views/News1.vue";
import NewsDetail1 from "@/components/News/NewsDetail1.vue"
import Member1 from "../views/Member1.vue";
import MemberLogin1 from "../components/Member/MemberLogin1.vue";
import MemberRegister1 from "../components/Member/MemberRegister1.vue";
import MemberDetail1 from "../components/Member/MemberDetail1.vue";
import MemberUpdate1 from "../components/Member/MemberUpdate1.vue";
import MemberFind1 from "../components/Member/MemberFind1.vue";

const routes = [
  {
    path: "/",
    redirect: "/home",
    component: DashboardLayout,
    children: [
      {
        path: "/home",
        name: "Home",
        components: { default: Home1 },
      },
      {
        path: "/trade",
        name: "Trade",
        components: { default: Trade1 },
      },
      {
        path: "/like",
        name: "Like",
        components: { default: Like1 },
      },
      {
        path: "/news",
        name: "News",
        components: { default: News1 },
      },
      {
        path: "/news/:listno",
        name: "NewsDetail",
        components: { default: NewsDetail1 },
      },
      {
        path: "/board",
        name: "Board",
        components: { default: Board1 },
      },
      {
        path: "/board/:listno",
        name: "BoardDetail",
        components: { default: BoardDetail1 },
        },
        {
          path: "/board/register",
          name: "BoardRegister",
          components: { default: BoardRegister1 },
      },
      {
        path: "/board/update",
        name: "BoardUpdate",
        components: { default: BoardUpdate1 },
      },
      {
        path: "/member",
        name: "Member",
        components: { default: Member1 },
        redirect: "/member/register",
      },
      {
        path: "/member/register",
        name: "MemberRegister",
        components: { default: MemberRegister1 },
      },
      {
        path: "/member/detail",
        name: "MemberDetail",
        components: { default: MemberDetail1 },
      },
      {
        path: "/member/findPW",
        name: "MemberFind",
        components: { default: MemberFind1 },
      },
      {
        path: "/member/update",
        name: "MemberUpdate",
        components: { default: MemberUpdate1 },
      },
      {
        path: "/member/login",
        name: "MemberLogin",
        components: { default: MemberLogin1 },
      },
    ],
  },
  
  


];

const router = createRouter({
  history: createWebHashHistory(),
  linkActiveClass: "active",
  routes,
});

export default router;
