<template>
  <div>
    <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
      <div class="row">
        <div class="col-xl-3 col-lg-6"></div>
      </div>
    </base-header>

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col">
          <div class="card shadow border-0">
            <div class="card_inner">
              <table class="table">
                <tbody id="table_inner">
                  <tr>
                    <td style="width: 10%" class="replytd"></td>
                    <td style="width: 15%" class="replytd"></td>
                    <td style="width: 5%" class="replytd">아이디</td>
                    <td style="width: 40%" class="replytd">
                      <div class="form-group">
                        <label for="title_label"></label>
                        <input
                          ref="ID_ref"
                          type="text"
                          class="form-control"
                          placeholder="ID를 입력해주세요.."
                          v-model="find_user.MemberId"
                          id="title1"
                        />
                      </div>
                    </td>
                    <td style="width: 30%" class="replytd"></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <!-- <button
            type="button"
            class="btn btn-primary"
            v-on:click="goback()"
            id="goback1"
          >
            돌아가기
          </button> -->
          <button
            type="button"
            class="btn btn-success"
            v-on:click="findpassword()"
            id="enroll1"
          >
            찾기
          </button>
        </div>
      </div>
    </div>
    <div class="empty-box"></div>
  </div>
</template>

<script>
import http from "@/utils/http-common.js";
import { mapMutations } from "vuex";

export default {
  created() {},
  components: {},
  data() {
    return {
      find_user: { MemberId: "" },
    };
  },
  methods: {
    ...mapMutations(["SET_LOGIN_STATE"]),
    gogo(num) {
      alert(num);
    },
    goback() {
      this.$router.go(-1);
    },
    findpassword() {
      let url = "/member/findpw";
      http
        .post(url, {
          memberId: this.find_user.MemberId,
        })
        .then(({ data }) => {
          let msg = "오류가 발생하였습니다.";
          if (data === "success") {
            msg = "가입하신 메일로 임시 비밀번호가 전송되었습니다.";
            alert(msg);
            this.$router.go(-1);
          } else alert(msg);
        })
        .catch(() => {
          alert("가입된 아이디가 아닙니다.");
        });
    },
    MemLogin() {
      let err = true;
      let msg = "";
      !this.find_user.MemberId &&
        ((msg = "아이디를 입력해주세요"),
        (err = false),
        this.$refs.ID_ref.focus());
      err &&
        !this.find_user.MemberPw &&
        ((msg = "비밀번호를 입력해주세요"),
        (err = false),
        this.$refs.PWD_ref.focus());

      if (!err) alert(msg);
      else {
        http
          .post(`/member/login`, {
            memberId: this.find_user.MemberId,
            memberPw: this.find_user.MemberPw,
          })
          .then(({ data }) => {
            let msg = "아이디 혹은 비밀번호를 확인해주세요.";
            if (data.message === "success") {
              msg = "로그인 성공";
              alert(msg);

              this.SET_LOGIN_STATE({
                id: this.find_user.MemberId,
                name: "Kim",
              });
              this.$router.push({ name: "Home" });
            } else alert(msg);
          });
      }
    },
  },
};
</script>

<style scoped>
.bg-gradient-success {
  background: linear-gradient(87deg, #2ac1e7 0, #f16d99 100%) !important;
}

.table_font {
  font-size: 15px;
}

#edit2 {
  margin-left: 5px;
  height: 39px;
}

#search_div {
  margin-left: 30%;
}

#edit1 {
  float: left;
}

#goback1 {
  float: left;
  margin-top: 15px;
}

#enroll1 {
  float: left;
  margin-top: 15px;
}

#input1 {
  margin-top: 15px;
  margin-left: 40px;
}

#sel1 {
  float: left;
  width: 100px;
  height: 40px;
  font-size: 16px;
}

#inp1 {
  float: left;
  width: 300px;
  height: 40px;
  font-size: 16px;
}

#commentsbox {
  margin-top: 30px;
}

#commentsin {
  margin-top: 10px;
}

#commentcreate {
  float: left;
  width: 9%;
  height: 105px;
}

#content1 {
  float: left;
  resize: none;
}

.replytd {
  vertical-align: middle;
  padding: 1px 1px;
}

.replytd2 {
  vertical-align: middle;
}

.form-control:focus {
  border-color: #08c9eb;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(145, 148, 192, 0.6);
}

.empty-box {
  margin-top: 800px;
}
</style>
