<template>
  <div>
    <base-header type="" class="pb-6 pb-8 pt-5 pt-md-8">
      <!-- Card stats -->
      <div class="row">
        <div
          class="col-xl-2 col-lg-6 cardbox1"
          v-for="(top_board, index) in top_boards"
          :key="index"
        >
          <stats-card
            :title="top_board.boardNo"
            type="gradient-red"
            :sub-title="top_board.boardTitle"
            :subContent="top_board.memberId"
            :subBoardCnt="top_board.boardCnt"
            icon="ni ni-active-40"
            class="mb-4 mb-xl-0 card1s"
            v-on:click="goDetail(top_board.boardNo)"
          >
            <template v-slot:footer>
              <span class="text-success mr-2"
                ><i class="fa fa-arrow-up"></i>
              </span>
              <span class="text-nowrap">{{ top_board.date }}</span>
            </template>
          </stats-card>
        </div>
      </div>
    </base-header>

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col">
          <div class="card shadow">
            <div class="card-body">
              <!--board 내용 시작-->
              <div class="row icon-examples">
                <table class="table table-hover">
                  <thead>
                    <tr>
                      <th style="width: 10%" class="table_font">번호</th>
                      <th style="width: 60%" class="table_font">제목</th>
                      <th style="width: 10%" class="table_font">작성자</th>
                      <th style="width: 10%" class="table_font">조회수</th>
                      <th style="width: 10%" class="table_font">작성일</th>
                    </tr>
                  </thead>
                  <tbody v-for="(board, index) in paginatedData" :key="index">
                    <tr v-on:click="goDetail(board.boardNo)">
                      <td class="table_font">{{ board.boardNo }}</td>
                      <td class="table_font">{{ board.boardTitle }}</td>
                      <td class="table_font">{{ board.memberId }}</td>
                      <td class="table_font">{{ board.boardCnt }}</td>
                      <td class="table_font">{{ board.boardRecent }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <!--board 내용 끝 -->
              <div class="btn-cover">
                <button
                  :disabled="pageNum === 0"
                  @click="prevPage"
                  class="page-btn rounded"
                >
                  이전
                </button>
                <span class="page-count"
                  >{{ pageNum + 1 }} / {{ pageCount }} 페이지</span
                >
                <button
                  :disabled="pageNum >= pageCount - 1"
                  @click="nextPage"
                  class="page-btn rounded"
                >
                  다음
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="input1">
      <div>
        <button
          type="button"
          class="btn btn-primary"
          v-on:click="goEdit()"
          id="edit1"
        >
          글작성
        </button>
        <div class="form-group" id="search_div">
          <select v-model="selected" class="form-control" id="sel1">
            <option v-for="select1 in select_list" v-bind:key="select1.value">
              {{ select1.text }}
            </option>
          </select>
          <!-- <span> {{ selected }} </span> -->
          <input
            type="text"
            class="form-control"
            id="inp1"
            v-model="searchbar"
          />
        </div>
        <button
          type="button"
          class="btn btn-primary"
          v-on:click="gosearch(searchbar)"
          id="edit2"
        >
          검색
        </button>
      </div>
    </div>
    <div class="empty-box"></div>
  </div>
</template>

<script>
import http from "@/utils/http-common.js";
import { mapGetters } from "vuex";

export default {
  created() {
    http.get(`/board`).then(({ data }) => {
      this.boards = data;

      var temp_boards2 = JSON.parse(JSON.stringify(this.boards));
      this.top_boards = temp_boards2
        .sort(function (a, b) {
          return b.boardCnt - a.boardCnt;
        })
        .slice(0, 4);
    });
  },
  components: {},
  computed: {
    ...mapGetters(["getLoginState"]),
    pageCount() {
      let listLeng = this.boards.length,
        listSize = this.pageSize,
        page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;

      /*
      아니면 page = Math.floor((listLeng - 1) / listSize) + 1;
      이런식으로 if 문 없이 고칠 수도 있다!
      */
      return page;
    },
    paginatedData() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize;
      return this.boards.slice(start, end);
    },
  },
  data() {
    return {
      icons: [],
      boards: [],
      top_boards: [],
      select_list: [
        { text: "글제목", value: "boardTitle" },
        { text: "작성자", value: "memberId" },
      ],
      selected: "작성글",
      pageNum: 0,
      pageSize: 8,
      searchbar: "",
    };
  },
  methods: {
    nextPage() {
      this.pageNum += 1;
    },
    prevPage() {
      this.pageNum -= 1;
    },
    gogo(num) {
      alert(num);
    },
    goEdit() {
      if (!this.getLoginState) alert("로그인해주세요.");
      else {
        this.$router.push({ name: "BoardRegister" });
      }
    },
    goDetail(num) {
      this.$router.push({
        name: "BoardDetail",
        params: { listno: num },
      });
    },
    gosearch(str) {
      var temp_key = "";
      if (this.selected == "글제목") temp_key = "boardTitle";
      else if (this.selected == "작성자") temp_key = "memberId";

      http
        .get(`/board`, { params: { key: temp_key, word: str } })
        .then(({ data }) => {
          this.boards = data;
        });
    },
  },
};
</script>

