<template>
  <div>
    <button class="btn btn-primary btn1" v-on:click="goback()">돌아가기</button>
    <button class="btn btn-success btn1" v-on:click="like1()">
      관심지역 등록
    </button>
  </div>
  <table class="table table-hover">
    <tbody v-for="(value, index) in trade_val2" :key="index">
      <tr>
        <td style="width: 30%" class="table_font">{{ index }}</td>
        <td style="width: 70%" class="table_font">{{ value }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import http from "@/utils/http-common.js";
import { mapGetters } from "vuex";

export default {
  created() {
    let url = "/trade/" + this.trade_detail1_num;
    http.get(url).then(({ data }) => {
      this.trade_val = data;

      this.trade_val2["등록번호"] = this.trade_val["tradeId"];
      this.trade_val2["시구군"] = this.trade_val["sigugun"];
      this.trade_val2["이름"] = this.trade_val["houseName"];
      this.trade_val2["거래종류"] = this.trade_val["dealType"];
      this.trade_val2["건물종류"] = this.trade_val["houseType"];
      this.trade_val2["면적"] = this.trade_val["areaSize"];
      this.trade_val2["가격"] = this.trade_val["dealPrice"];
      this.trade_val2["층"] = this.trade_val["floor"];
      this.trade_val2["건축년도"] = this.trade_val["constructYear"];
      this.trade_val2["도로주소명"] = this.trade_val["roadAddr"];
    });
  },
  data() {
    return {
      trade_val: { sigugun: "hello" },
      trade_val2: {},
    };
  },
  methods: {
    goback() {
      this.$emit("listen_check1");
    },
    like1() {
      if (this.getLoginState == false) {
        alert("로그인이 필요합니다.");
      } else {
        http
          .post("/locfavor", {
            memberId: this.getUserId,
            tradeId: this.trade_val["tradeId"],
          })
          .then(({ data }) => {
            let msg = "등록 처리시 문제가 발생했습니다.";
            if (data === "success") {
              msg = "관심지역으로 등록되었습니다.";
            }
            alert(msg);
          });
      }
    },
  },
  props: ["trade_detail1_num"],
  computed: {
    ...mapGetters(["getUserId", "getLoginState"]),
  },
};
</script>

<style>
.btn1 {
  margin-top: 3px;
  margin-left: 2px;
  margin-bottom: 5px;
}
</style>