          <button
            type="button"
            class="btn btn-success"
            v-on:click="goMemUpdate()"
            id="enroll1"
          >
            회원수정
          </button>
          <button
            type="button"
            class="btn btn-danger"
            v-on:click="goMemDelete()"
            id="enroll1"
          >
            회원탈퇴
          </button>
        </div>
      </div>
    </div>
    <div class="empty-box"></div>
  </div>
</template>

<script>
import http from "@/utils/http-common.js";
import { mapGetters, mapMutations } from "vuex";

export default {
  created() {
    let url = "/member/" + this.getUserId;
    http.get(url).then(({ data }) => {
      this.user_info = data;
    });
  },
  computed: {
    ...mapGetters(["getUserId"]),
    // sidos() {
    //   return this.$store.state.sidos;
    // },
  },
  components: {},
  data() {
    return {
      user_info: {
        MemberId: "",
        MemberName: "",
        MemberEmail: "",
      },
    };
  },
  methods: {
    ...mapMutations(["SET_LOGOUT_STATE"]),
    gogo(num) {
      alert(num);
    },
    goback() {
      this.$router.go(-1);
    },
    goMemUpdate() {
      this.$router.push({ name: "MemberUpdate", params: this.user_info });
    },
    goMemDelete() {
      let url = "/member/" + this.getUserId;
      http.delete(url).then(() => {
        this.SET_LOGOUT_STATE();
        alert("회원 탈퇴되었습니다.");
        this.$router.push({ name: "Home" });
      });
    },
  },
};
</script>

