import { createStore } from "vuex";
import createPersistedState from "vuex-persistedstate";

export default createStore({
  state: {
    login_state: false,
    user_id: null,
    user_name: null,
    bar_state: true,
  },
  getters: {
    getLoginState(state) {
      return state.login_state;
    },
    getUserId(state) {
      return state.user_id;
    },
    getUserName(state) {
      return state.user_name;
    },
    getBarState(state) {
      return state.bar_state;
    }
  },
  mutations: {
    SET_LOGIN_STATE(state, data) {
      state.login_state = true;
      state.user_id = data.id;
      state.user_name = data.name;
    },
    SET_LOGOUT_STATE(state) {
      state.login_state = false;
      state.user_id = null;
      state.user_name = null;
    },
    SET_BAR_TRUE(state) {
      state.bar_state = true;
    },
    SET_BAR_FALSE(state) {
      state.bar_state = false;
    }
  },
  actions: {
  },
  plugins: [createPersistedState()],
});